import React, { useState } from 'react';
import {
  Box,
  Typography,
  Button,
  TextField,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import Dropzone from 'react-dropzone';
import FlexBetween from '../../components/FlexBetween';
import { setLogin } from '../../state';

const initialValuesRegister = {
  firstName: '',
  lastName: '',
  email: '',
  password: '',
  location: '',
  occupation: '',
  picture: '',
};

const initialValuesLogin = {
  email: '',
  password: '',
};

const Form = () => {
  const [pageType, setPageType] = useState('login');
  const [formValues, setFormValues] = useState(
    pageType === 'login' ? initialValuesLogin : initialValuesRegister
  );
  const { palette } = useTheme();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const isNonMobile = useMediaQuery('(min-width: 600px)');
  const isLogin = pageType === 'login';
  const isRegister = pageType === 'register';

  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setFormValues((prevValues) => ({
      ...prevValues,
      [name]: value,
    }));
  };

  const handlePictureDrop = (acceptedFiles) => {
    setFormValues((prevValues) => ({
      ...prevValues,
      picture: acceptedFiles[0],
    }));
  };

  const register = async () => {
    // Simulate form submission for registration
    const formData = new FormData();
    for (let value in formValues) {
      formData.append(value, formValues[value]);
    }
    formData.append('picturePath', formValues.picture.name);

    // ... Handle the API request and response for registration
    // Example:
    const savedUserResponse = await fetch('/auth/register', {
      method: 'POST',
      body: formData,
    });
    const savedUser = await savedUserResponse.json();

    // onSubmitProps.resetForm();

    setPageType('login');
  };

  const login = async () => {
    // Simulate form submission for login
    // ... Handle the API request and response for login
    // Example:
    const loggedInResponse = await fetch('/auth/login', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(formValues),
    });
    const loggedIn = await loggedInResponse.json();

    // onSubmitProps.resetForm();

    // Dispatch login action and navigate to home page
    if (loggedIn) {
      dispatch(
        setLogin({
          user: loggedIn?.user,
          token: loggedIn?.token,
        })
      );
      navigate('/home');
    }
  };

  const handleFormSubmit = async (e) => {
    e.preventDefault();
    if (isLogin) await login();
    if (isRegister) await register();
  };

  return (
    <form onSubmit={handleFormSubmit}>
      <Box
        display='grid'
        gap='30px'
        gridTemplateColumns='repeat(4, minmax(0, 1fr))'
        sx={{
          '& > div': { gridColumn: isNonMobile ? undefined : 'span 4' },
        }}
      >
        {isRegister && (
          <>
            <TextField
              label='First Name'
              onBlur={handleFormChange}
              onChange={handleFormChange}
              value={formValues.firstName}
              name='firstName'
              sx={{ gridColumn: 'span 2' }}
            />
            <TextField
              label='Last Name'
              onBlur={handleFormChange}
              onChange={handleFormChange}
              value={formValues.lastName}
              name='lastName'
              sx={{ gridColumn: 'span 2' }}
            />
            <TextField
              label='Location'
              onBlur={handleFormChange}
              onChange={handleFormChange}
              value={formValues.location}
              name='location'
              sx={{ gridColumn: 'span 4' }}
            />
            <TextField
              label='Occupation'
              onBlur={handleFormChange}
              onChange={handleFormChange}
              value={formValues.occupation}
              name='occupation'
              sx={{ gridColumn: 'span 4' }}
            />
            <Box
              gridColumn='span 4'
              border={`1px solid ${palette.neutral.medium}`}
              borderRadius='5px'
              p='1rem'
            >
              <Dropzone
                acceptedFiles='.jpg,.jpeg,.png'
                multiple={false}
                onDrop={handlePictureDrop}
              >
                {({ getRootProps, getInputProps }) => (
                  <Box
                    {...getRootProps()}
                    border={`2px dashed ${palette.primary.main}`}
                    p='1rem'
                    sx={{ '&:hover': { cursor: 'pointer' } }}
                  >
                    <input {...getInputProps()} />
                    {!formValues.picture ? (
                      <p>Add Picture here</p>
                    ) : (
                      <FlexBetween>
                        <Typography>{formValues.picture.name}</Typography>
                        <EditOutlinedIcon />
                      </FlexBetween>
                    )}
                  </Box>
                )}
              </Dropzone>
            </Box>
          </>
        )}

        <TextField
          label='Email'
          onBlur={handleFormChange}
          onChange={handleFormChange}
          value={formValues.email}
          name='email'
          sx={{ gridColumn: 'span 4' }}
        />
        <TextField
          label='Password'
          type='password'
          onBlur={handleFormChange}
          onChange={handleFormChange}
          value={formValues.password}
          name='password'
          sx={{ gridColumn: 'span 4' }}
        />
      </Box>
      {/* Buttons */}
      <Box>
        <Button
          fullWidth
          type='submit'
          sx={{
            m: '2rem 0',
            p: '1rem',
            backgroundColor: palette.primary.main,
            color: palette.background.alt,
            '&:hover': { color: palette.primary.main },
          }}
        >
          {isLogin ? 'LOGIN' : 'REGISTER'}
        </Button>
        <Typography
          onClick={() => {
            setPageType(isLogin ? 'register' : 'login');
            setFormValues(
              isLogin ? initialValuesRegister : initialValuesLogin
            );
          }}
          sx={{
            textDecoration: 'underline',
            color: palette.primary.main,
            '&:hover': {
              cursor: 'pointer',
              color: palette.primary.light,
            },
          }}
        >
          {isLogin
            ? `Don't have an account? Sign Up here.`
            : `Already have an account? Login here.`}
        </Typography>
      </Box>
    </form>
  );
};

export default Form;
